html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
  height: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  min-height: 100%;
  display: grid;
  grid-template-rows: min-content auto min-content;
  font-family: "Poppins", sans-serif;
  background-color: #FAFAFA;
}

p, a, li, button, .ionicicono, th, td, label, input, select, textarea {
  font-size: 1.6rem;
}
@media (min-width: 450px) {
  p, a, li, button, .ionicicono, th, td, label, input, select, textarea {
    font-size: min(3.5vw, 2.2rem);
  }
}

input, select, textarea {
  padding: 0.25em 1em;
  background-color: transparent;
  border: 0.1rem solid #7a7a7a;
  width: 100%;
}

h1, h2, h3 {
  margin-top: 0;
}

h1 {
  font-size: 2.8rem;
}
@media (min-width: 768px) {
  h1 {
    font-size: 4rem;
  }
}

h2 {
  font-size: 2.4rem;
}
@media (min-width: 450px) {
  h2 {
    font-size: min(5.33vw, 3.8rem);
  }
}

h3 {
  font-size: 1.9rem;
}
@media (min-width: 450px) {
  h3 {
    font-size: min(4.22vw, 3.2rem);
  }
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.contenedor-botones {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}

.container {
  max-width: 120rem;
  width: 90%;
  margin: 0 auto;
}

.centrado-flex {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  height: 100%;
}

/*Layout*/
.no-webp .inicio {
  background: url(../img/header.jpg);
}

.webp .inicio {
  background: url(../img/header.webp);
}

.header {
  background-size: cover;
  background-repeat: no-repeat;
  background-color: #fff;
  background-color: #8C1C03;
}
.header.inicio {
  background-position: center center;
  background-color: #fff;
  min-height: min(50vh, 70rem);
}
.header.inicio .contenido-header {
  display: flex;
  justify-content: space-between;
  align-items: 0;
  flex-direction: column;
  min-height: min(50vh, 70rem);
}
.header.inicio .contenido-header .heading-header {
  text-align: center;
  color: #fff;
}
@media (min-width: 768px) {
  .header.inicio .contenido-header .heading-header {
    text-align: left;
  }
}

.footer {
  background-color: #323232;
}
.footer p {
  color: #fff;
}

.barra {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 1em 0;
}
.barra .navegacion {
  visibility: hidden;
  position: absolute;
  z-index: -10;
}
.barra .desplegarNavegacion {
  visibility: visible;
  position: static;
  z-index: 1;
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
  }
  .barra .icon-tabler-menu-2 {
    display: none;
  }
  .barra .navegacion {
    visibility: visible;
    position: static;
    z-index: 1;
  }
}
.barra .logo {
  font-size: 4rem;
  color: #fff;
}
.barra .logo--bold {
  font-weight: bold;
}

.barra--footer {
  display: flex;
  justify-content: center;
  align-items: 0;
  flex-direction: column;
}
.barra--footer .barra .navegacion {
  visibility: visible;
  position: static;
  z-index: 1;
}
.barra--footer p {
  text-align: center;
}

.navegacion {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
  }
  .navegacion .ionicicono {
    order: 1;
  }
}
.navegacion a, .navegacion .ionicicono {
  color: #fff;
  text-align: center;
  margin-left: 0;
  display: block;
  cursor: pointer;
}
@media (min-width: 768px) {
  .navegacion a, .navegacion .ionicicono {
    display: inline-block;
    margin-left: 0.7em;
  }
}
@media (min-width: 1024px) {
  .navegacion a, .navegacion .ionicicono {
    margin-left: 1em;
  }
}

.nosotros {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 1em;
  padding: 3em 0;
}
.nosotros h3 {
  text-align: center;
  margin: 0;
}
@media (min-width: 768px) {
  .nosotros {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3em;
  }
  .nosotros h3 {
    grid-column: 1/3;
  }
}
@media (min-width: 1024px) {
  .nosotros {
    align-items: center;
  }
}

.sobre-nosotros-sec {
  padding: 3em 0;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 1em;
}
.sobre-nosotros-sec h3 {
  text-align: center;
  margin: 0;
}
@media (min-width: 768px) {
  .sobre-nosotros-sec {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2em;
  }
  .sobre-nosotros-sec h3 {
    grid-column: 1/4;
  }
}

.tarjeta-sobre {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
}
.tarjeta-sobre .tarjeta-sobre__titulo {
  text-transform: uppercase;
}
.tarjeta-sobre .icon-tabler {
  order: -1;
}
.tarjeta-sobre p:last-of-type {
  margin: 0;
}

.propiedad {
  padding: 3em 0;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 1em;
  align-items: center;
}
.propiedad h3, .propiedad .boton-secundario {
  grid-column: 1/2;
}
.propiedad .boton-secundario {
  width: fit-content;
}
@media (min-width: 1024px) {
  .propiedad {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2em;
    align-items: start;
  }
  .propiedad h3, .propiedad .boton-secundario {
    grid-column: 1/3;
  }
}

.caracteristicasPropiedad {
  width: 100%;
}
.caracteristicasPropiedad .caractHabitaciones {
  display: flex;
  justify-content: 0;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
}
.caracteristicasPropiedad .caractHabitaciones .iconoHab {
  display: flex;
  justify-content: 0;
  align-items: center;
  flex-direction: row;
  margin-left: 1.5em;
}
.caracteristicasPropiedad .caractHabitaciones .iconoHab p {
  margin: 0;
}
.caracteristicasPropiedad .caractHabitaciones .iconoHab .icon-tabler {
  max-width: 5rem;
  max-height: 5rem;
  min-width: 2rem;
}
.caracteristicasPropiedad .caractHabitaciones .iconoHab:first-of-type {
  margin-left: 0;
}

.prop {
  padding: 3em 0;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 1em;
}
.prop h3 {
  grid-column: 1/2;
  text-align: center;
}

.propiedades-sec--main {
  padding: 3em 0;
}

.propiedades-sec h3 {
  text-align: center;
}
.propiedades-sec .contenedor-boton {
  margin: 2em 0 0;
}
.propiedades-sec .contenedor-boton .boton-primario {
  display: inline-block;
}

.tarjetas-depto {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 1em;
}
@media (min-width: 768px) {
  .tarjetas-depto {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5em;
    justify-content: flex-end;
  }
  .tarjetas-depto .tarjeta-depto__titulo, .tarjetas-depto .boton-secundario {
    grid-column: 1/3;
  }
}
@media (min-width: 1024px) {
  .tarjetas-depto {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2em;
  }
  .tarjetas-depto .tarjeta-depto__titulo, .tarjetas-depto .boton-secundario {
    grid-column: 1/4;
  }
}

.no-webp .contactsect {
  background: url("../img/encuentra.jpg");
}

.webp .contactsect {
  background: url("../img/encuentra.webp");
}

.contactsect {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 10em 5em;
  color: #fff;
  word-wrap: break-word;
  text-align: center;
}

.nuestroblogsec {
  padding: 3em 0;
}
.nuestroblogsec h3 {
  text-align: center;
}

.entradasBlog {
  margin-bottom: 2em;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 1em;
}
@media (min-width: 768px) {
  .entradasBlog {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 2em;
  }
}

.entrada {
  border: 0.1rem solid #606060;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 1em;
}
@media (min-width: 768px) {
  .entrada {
    display: grid;
    grid-template-columns: 1fr 2.5fr;
    overflow: hidden;
    align-items: center;
  }
}
.entrada .boton-secundario {
  display: block;
  margin-bottom: 0.5em;
}

.entrada-contenido {
  margin: 0 1em;
}
@media (min-width: 768px) {
  .entrada-contenido {
    margin-left: 0;
  }
}
.entrada-contenido .entrada-contenido__titulo {
  font-weight: bold;
}

.no-webp .contacto {
  background: url("../img/destacada.jpg");
}

.webp .contacto {
  background: url("../img/destacada.webp");
}

.contacto {
  padding: 7em 0;
  margin-bottom: 3em;
}

.imagenEntrada {
  max-height: 100rem;
  width: auto;
  display: block;
  margin: 0 auto;
}

.contenidoEntrada {
  word-wrap: normal;
}

.adminPage {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 1em;
  padding: 3em 0;
}
.adminPage h1 {
  text-align: center;
}
@media (min-width: 768px) {
  .adminPage {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2em;
  }
  .adminPage h1 {
    grid-column: 1/3;
  }
  .adminPage .boton-secundario {
    min-height: 10rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
  }
}

.listado, .contenedor-formulario {
  display: flex;
  justify-content: 0;
  align-items: 0;
  flex-direction: column;
  padding: 3em 0;
}
.listado h1, .contenedor-formulario h1 {
  text-align: center;
  margin-bottom: 0;
}
.listado .boton-secundario, .listado .boton-primario, .contenedor-formulario .boton-secundario, .contenedor-formulario .boton-primario {
  width: fit-content;
  margin-top: 1em;
}

.edicionImagenes {
  padding: 3em 0;
  padding-top: 0;
  width: 100%;
}
.edicionImagenes h3 {
  text-align: center;
}

.contenedor-imagenes {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 1em;
}
@media (min-width: 768px) {
  .contenedor-imagenes {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2em;
  }
}

/*Componentes*/
.boton-primario {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  background-color: #8C1C03;
  color: #fff;
  padding: 0.5em 1em;
  display: inline-block;
  text-align: center;
  border: 0;
}
.boton-primario:hover {
  background-color: #731702;
  cursor: pointer;
}

.boton-secundario {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  background-color: #D97925;
  color: #fff;
  padding: 0.5em 1em;
  display: inline-block;
  text-align: center;
  border: 0;
}
.boton-secundario:hover {
  background-color: #c36d21;
  cursor: pointer;
}

.boton-eliminar {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  background-color: #B00020;
  color: #fff;
  padding: 0.5em 1em;
  display: inline-block;
  text-align: center;
  border: 0;
}
.boton-eliminar:hover {
  background-color: #97001b;
  cursor: pointer;
}

.aviso-error {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  background-color: #B00020;
  color: #fff;
  padding: 0.5em 1em;
  display: inline-block;
  text-align: center;
  border: 0;
}

.aviso-mensaje {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  background-color: #008f39;
  color: #fff;
  padding: 0.5em 1em;
  display: inline-block;
  text-align: center;
  border: 0;
}

.contenedorTabla {
  max-width: 100vw;
  border: 0.1rem solid #606060;
  margin: 2em 0 0;
  overflow: auto;
}

.tabla {
  width: 100%;
  border-collapse: collapse;
}
.tabla th {
  background-color: #323232;
  color: #fff;
}
.tabla th, .tabla td {
  border: 0.1rem solid #606060;
  text-align: center;
}
.tabla td.imagenProp {
  max-width: 10rem;
}
.tabla td img {
  width: 100%;
}
.tabla td a.editar {
  color: #174FBF;
}
.tabla td a.eliminar {
  color: #B00020;
}

.formulario {
  padding: 3em 0;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 1em;
  margin: 0 auto;
}
.formulario h1, .formulario h2, .formulario h3 {
  grid-column: 1/2;
  text-align: center;
  margin: 0;
}
.formulario .campo input[type=file] {
  border: 0;
}
.formulario .campo textarea {
  width: 100%;
}
.formulario .boton-secundario {
  display: block;
  width: 100%;
}

@media (min-width: 768px) {
  .formulario--2col {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2em;
  }
  .formulario--2col h1, .formulario--2col h2, .formulario--2col h3, .formulario--2col .boton-primario, .formulario--2col .boton-secundario, .formulario--2col .contenedorTabla, .formulario--2col .aviso-mensaje, .formulario--2col .aviso-error {
    grid-column: 1/3;
  }
  .formulario--2col .boton-secundario, .formulario--2col .boton-primario {
    width: fit-content;
  }
}

.formulario--3col {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1em;
}
.formulario--3col h1, .formulario--3col h2, .formulario--3col h3, .formulario--3col .boton-primario {
  grid-column: 1/3;
}
@media (min-width: 768px) {
  .formulario--3col {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2em;
  }
